import React, { useEffect, useState, Fragment } from 'react';
import MaterialTable from 'material-table';
import { getWorkers, getWorkerTrainings, updateWorker, getTrainings, deleteWorker, addTraining } from '../utils/APIUtils'
import { makeStyles } from '@material-ui/core/styles';
import RTL from '../utils/RTL';

import TextField from '@material-ui/core/TextField';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';

import WorkerForm from './WorkerForm';
import SelectMenu from './SelectMenu';
import AlertPopup from './AlertPopup';
import Loader from './Loader';

import Button from '@material-ui/core/Button';


const useStyles = makeStyles((theme) => ({
    root: {
        overflow: "hidden"
    },
    addTraining: {
        float: "right",
        color: "white",
        backgroundColor: "#4863f8",
        margin: "3px"
    },
    addWorker: {
        float: "right",
        color: "white",
        backgroundColor: "#4863f8",
        margin: "3px"
    },
}));

export default function WorkersTable() {
    const classes = useStyles();

    const [openDialog, setOpenDialog] = useState(false);
    const [openAlert, setOpenAlert] = useState(false);
    const [alertStatus, setAlertStatus] = useState(null);

    const [selectedWorker, setSelectedWorker] = useState();
    const [trainingsList, setTrainingsList] = useState([]);
    const [selectedTrainings, setSelectedTrainings] = useState([]);
    const [isLoaded, setIsLoaded] = useState(false);
    const [workers, setWorkers] = useState([]);
    const [workersToDisplay, setWorkersToDisplay] = useState([]);
    const [openTrainingDialog, setOpenTrainingDialog] = useState(false);
    const [trainingDialogText, setTrainingDialogText] = useState('');


    useEffect(() => {
        getAllWorkers();
        getAllTrainings();
    }, [])

    useEffect(() => {
        updateWorkersToDisplay();
    }, [workers, trainingsList]);

    useEffect(() => {
        updateWorkersToDisplay();
    }, [selectedTrainings]);


    const getAllWorkers = () => {
        getWorkers()
            .then(res => {
                setWorkers([...res.data]);
                setIsLoaded(true);
            })
            .catch(err => {
                console.error(err);
            });
    }

    const getAllTrainings = () => {
        getTrainings()
            .then(res => {
                setTrainingsList(res.data);
                setIsLoaded(true);
            })
            .catch(err => {
                console.error(err);
            });
    }

    const removeWorkerTrainings = (worker) => {
        return worker;
    }

    const openWorkerDialog = (worker) => {
        let trainings = [];
        getWorkerTrainings(worker.workerId)
            .then(res => {
                trainings = res.data.trainings
            })
            .catch(err => console.error(err))
            .then(() => {
                worker.trainings = trainings;
                setSelectedWorker(worker);
                setOpenDialog(true);
            });
    }

    const closeWorkerDialog = () => {
        setOpenDialog(false);
    }

    const openWorkerModifiedAlert = () => {
        const successText = 'עובד עודכן בהצלחה';
        const failedText = 'שגיאה בזמן עדכון העובד';
        return (<AlertPopup handleClose={() => { setOpenAlert(false); setAlertStatus(null) }} open={openAlert} message={alertStatus ? successText : failedText} success={alertStatus}></AlertPopup>);
    }

    const handleDeleteWorker = (worker) => {
        console.log(worker);
        if (worker?.workerId) {
            deleteWorker(worker.workerId)
                .then(res => {
                    getAllWorkers();
                })
        }
    }

    const updateWorkerInfo = (tempWorker) => {
        delete tempWorker["tableData"];
        updateWorker(tempWorker)
            .then(res => {
                setAlertStatus(true);
            })
            .catch(err => {
                setAlertStatus(false);
                console.error(err);
            })
            .then(() => {
                getAllWorkers();
                setOpenAlert(true);
            })
        closeWorkerDialog();
    }

    const workerCompletedTraining = (worker, trainingName) => {
        let isCompleted = false;
        (worker.trainings).forEach(training => {
            if (training.name == trainingName && training.status != "N/A" && training.status != "לא השתתף") {
                isCompleted = true;
            }
        });
        return isCompleted;
    }

    const updateWorkersToDisplay = () => {
        const tempWorkersToDisplay = [];
        workers.forEach(worker => {
            let completedAllTrainings = true;
            selectedTrainings.forEach((trainingName) => {
                if (!workerCompletedTraining(worker, trainingName)) {
                    completedAllTrainings = false;
                }
            });
            if (completedAllTrainings) {
                tempWorkersToDisplay.push(removeWorkerTrainings({ ...worker }));
            }
        });
        setWorkersToDisplay(tempWorkersToDisplay);
    }

    const trainingsChanged = (trainings) => {
        setSelectedTrainings(trainings);
    }

    const handleCloseTrainingDialog = () => {
        setTrainingDialogText('');
        setOpenTrainingDialog(false);
    }
    const handleSaveTrainingDialog = () => {
        console.log(trainingDialogText);
        addTraining(trainingDialogText)
            .then(res => {
                console.log(res);
                //TODO: show dialog with success adding training text
            })
            .catch(err => {
                console.error(err);
            })
            .then(() => {
                setTrainingDialogText('');
                setOpenTrainingDialog(false);
                getAllTrainings();
            })

    }



    const fields = [
        { field: 'workerId', title: 'מספר עובד' },
        { field: 'firstName', title: 'שם פרטי' },
        { field: 'lastName', title: 'שם משפחה' },
        { field: 'birthDate', title: 'תאריך לידה', type: 'date' },
        { field: 'age', title: 'גיל' },
        { field: 'sex', title: 'מין' },
        { field: 'startDate', title: 'תאריך התחלה' },
        { field: 'idNumber', title: 'תעודת זהות' },
        { field: 'phoneNumber', title: 'טלפון' },
        { field: 'phoneNumber2', title: 'טלפון 2' },
        { field: 'city', title: 'עיר' },
        { field: 'address', title: 'כתובת' },
        { field: 'zipCode', title: 'מיקוד' },
        { field: 'departmentHE', title: 'מחלקה' },
        { field: 'departmentEN', title: 'Department' },
        { field: 'roleEmergency', title: 'תפקיד חרום' },
        { field: 'role', title: 'תפקיד' },
        { field: 'team', title: 'תת מחלקה' },
    ]

    const emptyWorker = {
        "workerId": "",
        "lastName": "",
        "firstName": "",
    }

    if (!isLoaded) {
        return <div />;
    }
    if (isLoaded) {
        return (
            <div>
                <RTL>
                    <div className={classes.root}>
                        <SelectMenu className={classes.selectMenu} trainingsList={trainingsList} onTrainingsChanged={trainingsChanged} />
                        <Button className={classes.addTraining} onClick={() => setOpenTrainingDialog(true)} variant="contained">הוספת הדרכה</Button>
                        <Button className={classes.addWorker} onClick={() => openWorkerDialog(emptyWorker)} variant="contained">הוספת עובד</Button>
                    </div>
                </RTL>
                <RTL>
                    <MaterialTable
                        title="עובדים"
                        columns={fields}
                        data={workersToDisplay}
                        dir="rtl"
                        localization={{
                            pagination: {
                                labelDisplayedRows: '{from}-{to} מתוך {count}'
                            },
                            toolbar: {
                                nRowsSelected: '{0} שורות נבחרו'
                            },
                            header: {
                                actions: 'פעולות'
                            },
                            body: {
                                emptyDataSourceMessage: <Loader />,
                                filterRow: {
                                    filterTooltip: 'סינון'
                                },
                                addTooltip: 'הוספה',
                                deleteTooltip: 'מחיקה',
                                editTooltip: 'עריכה',
                                editRow: {
                                    deleteText: 'האם אתה בטוח שברצונך למחוק שורה זו?',
                                    cancelTooltip: 'ביטול',
                                    saveTooltip: 'שמור'
                                }
                            },
                            pagination: {
                                labelRowsSelect: 'שורות'
                            },
                            toolbar: {
                                searchTooltip: 'חיפוש',
                                searchPlaceholder: 'חיפוש'
                            }
                        }}
                        editable={{
                            onRowDelete: oldData =>
                                new Promise((resolve, reject) => {
                                    setTimeout(() => {
                                        handleDeleteWorker(oldData);
                                        resolve()
                                    }, 1000)
                                }),
                        }}
                        actions={[
                            {
                                icon: 'info',
                                tooltip: 'פרטים נוספים',
                                onClick: (event, rowData) => {
                                    openWorkerDialog(rowData);
                                }
                            },
                        ]}
                    />
                    {openDialog ?
                        <WorkerForm worker={selectedWorker} trainingsList={trainingsList} open={openDialog} onClose={closeWorkerDialog} onSaveChanges={(tempWorker) => updateWorkerInfo(tempWorker)} />
                        :
                        null
                    }
                    {openAlert ?
                        openWorkerModifiedAlert()
                        :
                        null
                    }
                </RTL>
                <Dialog open={openTrainingDialog} onClose={handleCloseTrainingDialog} aria-labelledby="form-dialog-title">
                    <DialogTitle id="form-dialog-title">הוספת הדרכה</DialogTitle>
                    <DialogContent>
                        <TextField
                            autoFocus
                            margin="dense"
                            id="name"
                            label="שם הדרכה"
                            type="text"
                            fullWidth
                            onChange={e => setTrainingDialogText(e.target.value)}
                        />
                    </DialogContent>
                    <DialogActions>
                        <Button onClick={handleCloseTrainingDialog} color="primary">
                            ביטול
                    </Button>
                        <Button onClick={handleSaveTrainingDialog} color="primary">
                            הוספה
                    </Button>
                    </DialogActions>
                </Dialog>
            </div>

        )
    }
}
