import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import { logo } from '../assets/emerson_logo.png';
import Box from '@material-ui/core/Box';
import Grid from '@material-ui/core/Grid';



const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  menuButton: {
    marginRight: theme.spacing(2),
  },
  title: {
    flexGrow: 1,
  },
  emersonLogo: {
    maxWidth: '120px'
  },
  spectLogo: {
    marginTop: '35px',
    maxWidth: '120px'
  }
}));

export default function ButtonAppBar() {
  const classes = useStyles();

  return (
    <div className={classes.root}>
      <AppBar position="static">
        <Toolbar>
          <Grid container spacing={4} justify="space-between">
            <Grid item xs={4}>
              <div style={{textAlign:"right"}}>
                <IconButton edge="start" className={classes.menuButton} color="inherit" aria-label="menu">
                  <MenuIcon />
                </IconButton>
              </div>
            </Grid>
            <Grid item xs={4}>
              <Typography variant="h6" className={classes.title}>
                מערכת ניהול עובדים והדרכות
              </Typography>
            </Grid>
            <Grid item xs={2}>
              <div style={{textAlign:"left"}}>
                <img className={classes.emersonLogo} src={require('../assets/emerson_logo.png')} />
              </div>
            </Grid>
            <Grid item xs={2}>
              <div style={{textAlign:"right"}}>
                <img className={classes.spectLogo} src={require('../assets/spectronix_logo.png')} />
              </div>
            </Grid>
          </Grid>
        </Toolbar>
      </AppBar>
    </div>
  );
}